.solution-wrap {
    .desc-id {
        margin-top:5px
    }
        .alink {
            color: #1A73E8;
            padding-right: 15px;
            font-size: 12px;
        }
        .update-date {
            color: #797979;
            font-size: 11px;
        }
        .card-text b{ 
            vertical-align: middle;
            font-size: 12px;
        }
    .desc-answer {
        text-overflow: ellipsis;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        margin-top: 5px;
    }
    .icon-list {
        padding: 0.5rem 1rem;

            }
    .icon-list ul {
        display: flex;
        flex-direction: row;
        margin: 0;
        padding: 0;
        
        text-align: center;
        gap:10px
    }

        .icon-list li {
            list-style: none;
            font-size: 12px;
            display: flex;
    align-items: center;
           
        }
        .icon-list span {
            display: flex;
        }

        .icon-list li:nth-child(1) {
            padding-left: 0;
        }
       
        .icon-list img {
            vertical-align: middle;
            margin-top: -3px;
        }
        .icon-list svg {
            vertical-align: middle;
            margin-top: -3px;
            text-align: center;
        }
        .icon-list li:nth-child(1) svg,.icon-list li:nth-child(2) svg{
            padding-right: 0px;
            padding-bottom: 2px;
        }
        .icon-list .generatebtn svg {
            width: 25px;
        }
    .MuiTabPanel-root {
        padding: 10px 0;
    }
  .updown-icons {
    padding: 0 0;
  }
   
        
        ul {
            display: flex;
            flex-direction: row;
            margin: 0;
            padding: 0;
            position: relative;
        .links {
            color: #1A73E8;
            font-weight: 600;
        }
        .dot-li {
            color: #bbb;
            padding-left: 10px;
            font-size: 6px;
            vertical-align: middle;
            display: inline-block;
            position: absolute;
            top: 14px;
            right: -4px;
        }
        b {
            display: block;
            text-align: center;
            font-weight: normal;
            color: 797979;
    
        }
    }
    }

    .cards {
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;
        padding: 0.5rem;

        .question-desc {
            width: 100%;
        }
        .count {
            color: #fff;
            width: 28px;
            height: 20px;
            text-align: center;
            vertical-align: middle;
            padding-top: 2px;
            border-radius: 4px;
            font-weight: 500;
            position: absolute;
            right: 12px;
            font-size: 11px;
        }

        
        .card {
            background-color: white;
            border-radius: 0.4rem;
            box-shadow: 0 9px 16px -12px rgba(0, 0, 0, 0.25);
            display: flex;
            flex-direction: column;
            margin-bottom: 20px;
            position: relative;
            min-width: 0;
            word-wrap: break-word;
            background-clip: border-box;
            border: 1px solid rgba(0, 0, 0, .125);

            .card-content {
                display: flex;
                flex: 1 1 auto;
                flex-direction: column;
                position: relative;

                .cards-link {
                    padding: 1rem 1rem 0 1rem;

                    .card-text {
                        flex: 1 1 auto;
                        font-size: 0.875rem;
                        line-height: 1.5;
                        margin: 0;
                        padding: 0;

                        b {
                            font-size: 13px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            vertical-align: middle;
                            display: inline-block;
                        }
                    }

                        

                        p {
                            font-size: 13px;
                        }

                        .desc-answer {
                            border-bottom: 1px solid #cdcdcd;
                            padding-bottom: 15px;
                            line-height: 22px;
                        }
                    
                }

                
            }

            .colored-dropdown {
                vertical-align: middle;
                margin-right: 5px;
                background-color: #5C828C;
                width: 12px;
                height: 12px;
                border-radius: 4px;
                display: inline-block;
            }
        }
    }
    .logout-wrap {
        position: fixed;
        top: 0;
       
        
        right:0;
        display: flex;
        justify-content: end;
        padding-top: 10px;
        z-index: 99;
    }
    .summarize-wrap ul {
        margin: 10px 0 16px 0;
        padding: 0px;
    }
    .summarize-wrap ul li {
        list-style: none;
        margin:5px 0;
        font-size: 13px;
    }
    .summarize-wrap  b {
        font-size: 12px; 
    }